import { usersAPI } from '../api/api';
const FOLLOW = 'FOLLOW';
const SET_USERS = 'SET-USERS';
const SET_CURRENT_PAGE = 'SET-CURRENT-PAGE';
const SET_TOTAL_USER_COUNT = 'SET-TOTAL-USER-COUNT';
const TOGGLE_IS_LOADING = 'TOGGLE-IS-LOADING';
const TOGGLE_IN_FOLLOWING = 'TOGGLE-IN-FOLLOWING';
let initState = {
    users: [],
    pageSize: 50,
    totalUsersCount: 0,
    currentPage: 1,
    isLoading: false,
    followingInProgress: [],
};
const usersReducer = (state = initState, action) => {
    switch (action.type) {
        case FOLLOW:
            return {
                ...state,
                users: state.users.map(u => {
                    if (u.id === action.userId) {
                        return {...u, followed: action.follow}
                    }
                    return u;
                }),
            };
        case SET_USERS:
            return {
                ...state,
                users: [...action.users],
            };
        case SET_CURRENT_PAGE:
            return {
                ...state,
                currentPage: action.currentPage,
            };
        case SET_TOTAL_USER_COUNT:
            return {
                ...state,
                totalUsersCount: action.totalUsersCount,
            };
        case TOGGLE_IS_LOADING:
            return {
                ...state,
                isLoading: action.isLoading,
            };
        case TOGGLE_IN_FOLLOWING:
            return {
                ...state,
                followingInProgress: action.isFetching ? [...state.followingInProgress, action.userId] :[state.followingInProgress.filter(id => id != action.userId)],
            };
        default:
            return state;
    }
}
export const toggleFollow = (userId, follow) => {
    return {
      type: FOLLOW,
      userId: userId,
      follow: follow
    }
}
export const setUsers = (users) => {
    return {
      type: SET_USERS,
      users: users
    }
}
export const setCurrentPage = (currentPage) => {
    return {
      type: SET_CURRENT_PAGE,
      currentPage: currentPage
    }
}
export const setTotalUsersCount = (totalUsersCount) => {
    return {
      type: SET_TOTAL_USER_COUNT,
      totalUsersCount: totalUsersCount
    }
}
export const toggleIsLoading = (isLoading) => {
    return {
      type: TOGGLE_IS_LOADING,
      isLoading: isLoading
    }
}
export const toggleInFollowing = (inFollowing, userId) => {
    return {
      type: TOGGLE_IN_FOLLOWING,
      followingInProgress: inFollowing,
      userId: userId,
    }
}

export const getUsersThunkCreator = (currentPage,pageSize) => async (dispatch) => {
    dispatch(toggleIsLoading(true));
    dispatch(setCurrentPage(currentPage));
    let data = await usersAPI.getUsers(currentPage,pageSize);
    dispatch(setUsers(data.items));
    dispatch(setTotalUsersCount(data.totalCount));
    dispatch(toggleIsLoading(false));
}
const toggleFollowThunkCreator = async (dispatch, userId, apiMethod, follow) => {
    dispatch(toggleInFollowing(true, userId));
    let data = await apiMethod(userId);
    if (data.resultCode == 0) {
        dispatch(toggleFollow(userId,follow));
    }
    dispatch(toggleInFollowing(false, userId));
}
export const followThunkCreator = (userId) => async (dispatch) => {
    toggleFollowThunkCreator(dispatch, userId, usersAPI.follow.bind(usersAPI), true);
}
export const unfollowThunkCreator = (userId) => async (dispatch) => {
    toggleFollowThunkCreator(dispatch, userId, usersAPI.unfollow.bind(usersAPI), false);
}
export default usersReducer;