const SEND_MESSAGE = 'ADD-MESSAGE';
let initState = {
    dialogs: [
        {id:1, name:"Dimych"},
        {id:2, name:"Sveta"},
        {id:3, name:"Alexandr"},
    ],
    messages: [
        {id:1, text:"How are you?", name:"Dimych"},
        {id:2, text:"How are you? How are you?", name:"Dimych"},
        {id:3, text:"Test message", name:"Dimych"},
    ],
};
const dialogsReducer = (state = initState, action) => {
    switch (action.type) {
        case SEND_MESSAGE:
            let newMessage = {
                id: 4,
                text: action.text,
                name: action.name,
            };
            return {
                ...state,
                newMessageText: '',
                messages: [...state.messages, newMessage]
            };
        default:
            return state;
    }
}

export const sendMessageActionCreator = (message) => {
    return {
      type: SEND_MESSAGE,
      text: message
    }
}

export default dialogsReducer;