import { authMeThunkCreator } from "./auth-reducer";

const SET_INITIAL = 'SET_INITIAL';
let initState = {
    initial: false,
};
const appReducer = (state = initState, action) => {
    switch (action.type) {
        case SET_INITIAL:
            return {
                ...state,
                initial: true
            };
        default:
            return state;
    }
}
export const setInitial = () => {
    return {
      type: SET_INITIAL,
    }
}
export const initialApp = () => {
    return (dispatch) => {
        let dispatchResult = dispatch(authMeThunkCreator());
        Promise.all([dispatchResult]).then(()=> {
            dispatch(setInitial());
        });
    }
}
export default appReducer;