import React from 'react'
import './Nav.css';
import {NavLink} from 'react-router-dom';
const Nav = () => {
  return (
    <nav className='nav'>
        <ul className='menu'>
          <li><NavLink className='menu__link' to="/profile">Profile</NavLink></li>
          <li><NavLink className='menu__link' to="/dialogs">Messages</NavLink></li>
          <li><NavLink className='menu__link' to="/users">Users</NavLink></li>
          <li><NavLink className='menu__link' to="/news">News</NavLink></li>
          <li><NavLink className='menu__link' to="/music">Music</NavLink></li>
          <li><NavLink className='menu__link' to="/settings">Settings</NavLink></li>
        </ul>
      </nav>
  )
}
export default Nav;